@use "../../styles/partials/mixins" as *;
@use "../../styles/partials/colors" as *;

.popup {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    overflow: auto;
    background-color: $primarydark+B3;
    justify-content: center;
 
    &__inner {
        background-color: $primarylight;
        width: 100%;
        height: 100%;
        position: relative;
        @include tablet {
            width: 672px;
            height: 262px; 
            margin-top: 185px
        }
    }
    &__title {
        @include header;
        padding: 3.75rem 5vw 0 5vw;
        @include tablet {
            padding-left: 4rem;
        }
    }
    &__detail {
        @include largebody;
        font-weight: 400;
        margin: 1rem 5vw;
        @include tablet {
            margin: 1rem 4rem;
        }
    }
    &__buttons {
        display: flex;
        justify-content: flex-end;
        position: absolute;
        bottom: 0;
        width: 100%;
    }
    &__cancel {
        padding: 0.5rem 3rem;
        border: 1px solid $secondarylight;
        box-sizing: border-box;
        border-radius: 20px;
        color: $secondarydark;
        margin: 1rem;
        cursor: pointer;
        @include tablet {
            padding: 8px 16px;
        }
    }
    &__delete {
        border: none;
        margin: 1rem;
        margin-left: 0;
        padding: 0.5rem 3rem;
        background: $supportlight;
        border-radius: 20px;
        cursor: pointer;
        @include tablet {
            padding: 8px 16px;
            margin-right: 4rem;
        }
    }
    &__close {
        padding: 12px;
        border: none;
        background-color: transparent;
        background-image: url(../../assets/icons/close-24px.svg);
        position: absolute;
        top: 1rem;
        right: 1rem;
        cursor: pointer;
    }
}